
import Vue from "vue";

export default Vue.extend({
  name: "side-navigation",
  model: {
    prop: "value",
    event: "click",
  },
  mixins: [],
  props: {
    value: String,
    items: {
      type: Array,
      default: () => [],
    },
    width: {
      type: String,
      default: "256",
    },
    color: {
      type: String,
      default: "light-blue lighten-3",
    },
    active: Boolean,
  },
  data() {
    return {
      lazyValue: this.value,
      drawer: this.active,
    };
  },
  methods: {},
  created() {
    //
  },
  computed: {
    internalValue: {
      set(val: string) {
        this.lazyValue = val;
        this.$emit("click", val);
      },
      get(): string {
        return this.lazyValue;
      },
    },
    dark: {
      get(): boolean {
        return this.$vuetify.theme.dark;
      },
    },
  },
  watch: {
    active(val: boolean) {
      this.drawer = val;
    },
    drawer(val: boolean) {
      this.$emit("update:active", val);
    },
  },
  components: {},
});
