
import Vue from "vue";

export default Vue.extend({
  name: "header-bar",
  model: {
    prop: "drawer",
    event: "click",
  },
  mixins: [],
  props: {
    drawer: Boolean,
    color: {
      type: String,
      default: "white",
    },
  },
  data() {
    return {
      lazyValue: this.drawer,
    };
  },
  methods: {},
  created() {
    //
  },
  computed: {
    internalValue: {
      set(val: boolean) {
        this.lazyValue = val;
        this.$emit("click", val);
      },
      get(): boolean {
        return this.lazyValue;
      },
    },
    dark: {
      set(val: boolean) {
        this.$vuetify.theme.dark = val;
      },
      get(): boolean {
        return this.$vuetify.theme.dark;
      },
    },
  },
  watch: {
    drawer(val: boolean) {
      this.internalValue = val;
    },
  },
  components: {},
});
